import React from "react";
// import '../../styles/User/Register.css'
import { get, post } from "../../util/api.js";
import validator from "validator";
import { connect } from "react-redux";
import "../../styles/Login_Register/Register.scss";

import {
  updateUserName,
  updateUserAvatar,
} from "../../store/actions/userActions";

class RegisterComponent extends React.Component {
  state = {};

  componentDidMount() {
    get("/user/islogin")
      .then((response) => {
        console.log(response);
        if (response.isLogin) {
          window.location = "/";
        }
      })
      .catch((error) => {
        console.error(error);

        // X l li nu c
      });
  }

  handleRegisterInput(key, e) {
    // console.log(e.target.value);
    this.setState({
      [key]: e.target.value,
    });
  }

  Validate() {
    var isValidate = true;

    let fullNameError = document.getElementById("fullNameError");
    let usernameError = document.getElementById("usernameError");
    let passwordError = document.getElementById("passwordError");
    let phoneNumberError = document.getElementById("phoneNumberError");
    let addressError = document.getElementById("addressError");
    let genderError = document.getElementById("genderError");

    fullNameError.textContent = "";
    usernameError.textContent = "";
    passwordError.textContent = "";
    phoneNumberError.textContent = "";
    addressError.textContent = "";
    genderError.textContent = "";

    const { full_name, username, password, phone_number, address, gender } =
      this.state;

    if (!full_name || validator.isEmpty(full_name)) {
      fullNameError.textContent = "H v Tn khng hp l";
      isValidate = false;
    }

    // Kim tra tn ng nhp
    if (!username || !validator.isLength(username, { min: 6 })) {
      usernameError.textContent = "Tn ng nhp phi c t nht 6 k t.";
      isValidate = false;
    }

    // Kim tra mt khu
    if (!password || !validator.isLength(password, { min: 6 })) {
      passwordError.textContent = "Mt khu phi c t nht 6 k t.";
      isValidate = false;
    }

    // Kim tra s in thoi
    if (!phone_number || !validator.isMobilePhone(phone_number, "vi-VN")) {
      phoneNumberError.textContent = "S in thoi khng hp l.";
      isValidate = false;
    }

    if (!address || validator.isEmpty(address)) {
      addressError.textContent = "a ch khng c b trng";
      isValidate = false;
    }

    if (!gender || validator.isEmpty(gender)) {
      genderError.textContent = "Gii tnh khng c b trng";
      isValidate = false;
    }

    return isValidate;
  }

  handleRegisterSubmit() {
    // console.log(this.Validate());

    if (this.Validate()) {
      post("/user/register", this.state)
        .then((response) => {
          console.log(response);
          window.location = "/";

          // X l d liu nhn c t server
        })
        .catch((error) => {
          if (error.response) {
            // console.error('Error:', error.response.data.error);
            // console.error('Additional Data:', error.response.data.additionalData);
            if (error.response.data.isExists) {
              // console.log('trung username');
              let usernameError = document.getElementById("usernameError");
              usernameError.textContent = "Username  tn ti.";
            }
          }
        });
    }
  }

  render() {
    return (
      <>
        {/* <div className="formdk">
                    <h2>NG K THNH VIN</h2>
                    <div className="form-group">
                        <label htmlFor="fullName">H v Tn:</label>
                        <input type="text" id="fullName" name="fullName" required onChange={(e) => this.handleRegisterInput('full_name', e)} />
                        <span id="fullNameError" className="error"></span>
                    </div>

                    <div className="form-group">
                        <label htmlFor="fullName">Tn ng nhp:</label>
                        <input type="address" id="address" name="address" required onChange={(e) => this.handleRegisterInput('username', e)} />
                        <span id="usernameError" className="error"></span>
                    </div>

                    <div className="form-group">
                        <label htmlFor="address">Mt khu:</label>
                        <input type="password" id="address" name="address" required onChange={(e) => this.handleRegisterInput('password', e)} />
                        <span id="passwordError" className="error"></span>
                    </div>

                    <div className="form-group">
                        <label htmlFor="phoneNumber">S in thoi:</label>
                        <input type="text" id="phoneNumber" name="phonenumber" required onChange={(e) => this.handleRegisterInput('phone_number', e)} />
                        <span id="phoneNumberError" className="error"></span>
                    </div>

                    <div className="form-group">
                        <label htmlFor="address">a ch:</label>
                        <input type="address" id="address" name="address" required onChange={(e) => this.handleRegisterInput('address', e)} />
                        <span id="addressError" className="error"></span>
                    </div>

                    <div className="form-group">
                        <label htmlFor="address">Gii tnh:</label>
                        <input name="gender" type="radio" value="male" onChange={(e) => this.handleRegisterInput('gender', e)} />Nam
                        <input name="gender" type="radio" value="female" onChange={(e) => this.handleRegisterInput('gender', e)} />N
                        <span id="genderError" className="error"></span>
                    </div>


                    <div className="form-group">
                        <button type="submit" name="submit" onClick={() => this.handleRegisterSubmit()}>ng k</button>
                    </div>
                </div>
 */}

        <div className="register-container">
          <div className="register">
            <div className="formdk">
              <h2>ng k thnh vin</h2>
              <div className="form-group">
                <label htmlFor="fullName">H v Tn:</label>
                <input
                  type="text"
                  id="fullName"
                  name="fullName"
                  required
                  onChange={(e) => this.handleRegisterInput("full_name", e)}
                />
                <span id="fullNameError" className="error"></span>
              </div>

              <div className="form-group">
                <label htmlFor="fullName">Tn ng nhp:</label>
                <input
                  type="address"
                  id="address"
                  name="address"
                  required
                  onChange={(e) => this.handleRegisterInput("username", e)}
                />
                <span id="usernameError" className="error"></span>
              </div>

              <div className="form-group">
                <label htmlFor="address">Mt khu:</label>
                <input
                  type="password"
                  id="address"
                  name="address"
                  required
                  onChange={(e) => this.handleRegisterInput("password", e)}
                />
                <span id="passwordError" className="error"></span>
              </div>

              <div className="form-group">
                <label htmlFor="phoneNumber">S in thoi:</label>
                <input
                  type="text"
                  id="phoneNumber"
                  name="phonenumber"
                  required
                  onChange={(e) => this.handleRegisterInput("phone_number", e)}
                />
                <span id="phoneNumberError" className="error"></span>
              </div>

              <div className="form-group">
                <label htmlFor="address">a ch:</label>
                <input
                  type="address"
                  id="address"
                  name="address"
                  required
                  onChange={(e) => this.handleRegisterInput("address", e)}
                />
                <span id="addressError" className="error"></span>
              </div>

              <div className="form-group">
                <label htmlFor="address">Gii tnh:</label>
                <input
                  name="gender"
                  type="radio"
                  value="male"
                  onChange={(e) => this.handleRegisterInput("gender", e)}
                />
                Nam
                <input
                  name="gender"
                  type="radio"
                  value="female"
                  onChange={(e) => this.handleRegisterInput("gender", e)}
                />
                N
                <span id="genderError" className="error"></span>
              </div>

              <div className="form-group">
                <button
                  type="submit"
                  name="submit"
                  onClick={() => this.handleRegisterSubmit()}
                >
                  ng k
                </button>
                <span className="psw">
                   c ti khon?<a href="/login"> ng nhp</a>
                </span>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

// export default RegisterComponent
export default RegisterComponent;
